*,
*::after,
*::before {
    box-sizing: border-box;
}

.disabled {
    pointer-events: none;
    background-color: #dfdfdf !important;
    -webkit-box-shadow: none;
    box-shadow: none;
    color: #9f9f9f !important;
    cursor: default;
}

main {
    --color-text: #000;
    --color-bg: #fff;
    --color-link: #d35066;
    --color-link-hover: #000;
    --color-info: #333;
    --color-menu: #031de6;
    --color-menu-hover: #000;
    --grid-columns: 2;
    --details-bg-up: #fff;
    --details-bg-down: #f5f0ef;
    --color-product-title: #000;
    --color-product-subtitle: #a09897;
    --color-details-title: #000;
    --color-details-subtitle: #000;
    --color-descr: #000;
    --color-price: var(--color-link);
    --color-bg-addtocart: #000;
    --color-addtocart: #fff;
    --color-close: #000;
    --color-bg-magnifier: #000;
    --color-magnifier: #fff;
    --color-btn-hover: #c1b3b1;

    position: relative;
    width: 100%;
    a {
        text-decoration: none;
        color: var(--color-link);
        outline: none;
    }

    a:hover,
    a:focus {
        color: var(--color-link-hover);
        outline: none;
    }

    button:focus {
        outline: none;
    }

    .content {
        position: relative;
        display: block;
    }

    .grid {
        margin: 10em auto 7em;
        position: relative;
        padding: 0 1em;
        width: 100%;
        max-width: 1000px;
        display: grid;
        grid-template-columns: repeat(var(--grid-columns), 1fr);
    }

    .grid__item {
        padding: 0 4vw;
        margin: 0 0 12vh;
    }

    .grid__item:nth-child(odd) .product {
        margin-top: -8em;
    }

    .product {
        height: 100%;
        padding-top: 10em;
        position: relative;
        cursor: pointer;
    }

    .product__bg {
        height: 20em;
        display: flex;
        justify-content: center;

        background: var(--details-bg-down);
        position: relative;
    }

    .product__img {
        max-height: 25em;
        margin: 0 auto;
        pointer-events: none;
    }

    .product__title {
        position: relative;
        margin: 0.5em 0 0;
        font-size: 1.75em;
        color: var(--color-product-title);
        font-family: "Playfair Display", serif;
        font-weight: 700;
    }

    .product__subtitle {
        position: relative;
        margin: 0;
        text-transform: uppercase;
        color: var(--color-product-subtitle);
        font-size: 0.85em;
        letter-spacing: 0.115em;
    }

    .product:hover .product__subtitle {
        color: var(--color-link);
    }

    .product__description,
    .product__price {
        opacity: 0;
        position: absolute;
    }

    .details {
        position: fixed;
        width: 100%;
        height: 100%;
        bottom: 0;
        left: 0;
        padding: 40vh 0 10vh 10vw;
        z-index: 101;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        pointer-events: none;
    }

    .details--open {
        pointer-events: auto;
    }

    .details > * {
        position: relative;
        opacity: 0;
    }

    .details__bg {
        width: 100%;
        position: fixed;
        left: 0;
    }

    .details__bg--up {
        top: 0;
        height: 100vh;
        background: var(--details-bg-up);
    }

    .details__bg--down {
        top: 40vh;
        height: 60vh;
        background: var(--details-bg-down);
    }

    .details__img {
        position: absolute;
        top: 7vh;
        right: -15vw;
        height: 80vh;
    }

    .details__bg,
    .details__img {
        transform-origin: 0 0;
    }

    .details__title {
        margin: -1.5em 0 0.1em;
        font-size: 4.5em;
        color: var(--color-details-title);
        font-family: "Playfair Display", serif;
        font-weight: 700;
    }

    .details__subtitle {
        text-transform: uppercase;
        margin: 0.75em 0 1em 0;
        letter-spacing: 0.115em;
        font-size: 1.75em;
        color: var(--color-details-subtitle);
    }

    .details__description {
        line-height: 1.5;
        font-weight: bold;
        max-width: 30%;
        margin: 2em 0 0 0;
        color: var(--color-details-desc);
    }

    .details__price {
        font-size: 3em;
        font-weight: bold;
        color: var(--color-price);
    }

    .details__close {
        position: absolute;
        top: 0;
        right: 0;
        border: 0;
        background: none;
        margin: 2em;
        cursor: pointer;
        font-size: 0.85em;
        color: var(--color-close);
    }

    @media screen and (max-width: 40em) {
        .grid {
            padding: 0 1vw;
        }
        .grid__item {
            padding: 0 2vw;
        }
        .product__bg {
            height: 10em;
        }
        .product__img {
            max-height: 19em;
        }
        .product__title {
            font-size: 1.25em;
        }
        .product__subtitle {
            font-size: 0.75em;
        }
        .details {
            padding: 30vh 0 5vh 10vw;
        }
        .details__bg--down {
            top: 30vh;
            height: 70vh;
        }
        .details__title {
            font-size: 2em;
        }
        .details__subtitle {
            font-size: 1em;
        }
        .details__price {
            font-size: 1.5em;
        }
        .details__description {
            max-width: 40%;
            font-size: 0.85em;
            margin: 1em 0 1em 0;
        }

        .details__img {
            right: -18vh;
        }
        .details__magnifier {
            right: 7vh;
        }
    }
}

.atc-button {
    --background: black;
    --text: #fff;
    --cart: #fff;
    --tick: var(--background);
    position: relative;
    border: none;
    background: none;
    padding: 8px 28px;
    border-radius: 8px;
    -webkit-tap-highlight-color: transparent;
    overflow: hidden;
    cursor: pointer;
    text-align: center;
    min-width: 144px;
    color: var(--text);
    background: var(--background);
    transform: scale(var(--scale, 1));
    transition: transform 0.4s cubic-bezier(0.36, 1.01, 0.32, 1.27);
    &:active {
        --scale: 0.95;
    }
    span {
        font-size: 14px;
        font-weight: 500;
        display: block;
        position: relative;
        padding-left: 24px;
        margin-left: -8px;
        line-height: 26px;
        transform: translateY(var(--span-y, 0));
        transition: transform 0.7s ease;
        &:before,
        &:after {
            content: "";
            width: var(--w, 2px);
            height: var(--h, 14px);
            border-radius: 1px;
            position: absolute;
            left: var(--l, 8px);
            top: var(--t, 6px);
            background: currentColor;
            transform: scale(0.75) rotate(var(--icon-r, 0deg))
                translateY(var(--icon-y, 0));
            transition: transform 0.65s ease 0.05s;
        }
        &:after {
            --w: 14px;
            --h: 2px;
            --l: 2px;
            --t: 12px;
        }
    }
    .cart {
        position: absolute;
        left: 50%;
        top: 50%;
        margin: -13px 0 0 -18px;
        transform-origin: 12px 23px;
        transform: translateX(-120px) rotate(-18deg);
        &:before,
        &:after {
            content: "";
            position: absolute;
        }
        &:before {
            width: 6px;
            height: 6px;
            border-radius: 50%;
            box-shadow: inset 0 0 0 2px var(--cart);
            bottom: 0;
            left: 9px;
            filter: drop-shadow(11px 0 0 var(--cart));
        }
        &:after {
            width: 16px;
            height: 9px;
            background: var(--cart);
            left: 9px;
            bottom: 7px;
            transform-origin: 50% 100%;
            transform: perspective(4px) rotateX(-6deg) scaleY(var(--fill, 0));
            transition: transform 1.2s ease var(--fill-d);
        }
        svg {
            width: 36px;
            height: 26px;
            display: block;
            position: relative;
            fill: none;
            stroke: var(--cart);
            stroke-width: 2px;
            stroke-linecap: round;
            stroke-linejoin: round;
            polyline {
                &:last-child {
                    stroke: var(--tick);
                    stroke-dasharray: 10px;
                    stroke-dashoffset: var(--offset, 10px);
                    transition: stroke-dashoffset 0.4s ease var(--offset-d);
                }
            }
        }
    }
    &.loading {
        --scale: 0.95;
        --span-y: -32px;
        --icon-r: 180deg;
        --fill: 1;
        --fill-d: 0.8s;
        --offset: 0;
        --offset-d: 1.73s;
        .cart {
            animation: cart 3.4s linear forwards 0.2s;
        }
    }
}

@keyframes cart {
    12.5% {
        transform: translateX(-60px) rotate(-18deg);
    }
    25%,
    45%,
    55%,
    75% {
        transform: none;
    }
    50% {
        transform: scale(0.9);
    }
    44%,
    56% {
        transform-origin: 12px 23px;
    }
    45%,
    55% {
        transform-origin: 50% 50%;
    }
    87.5% {
        transform: translateX(70px) rotate(-18deg);
    }
    100% {
        transform: translateX(140px) rotate(-18deg);
    }
}
